html {
  scrollbar-width: thin;
  scrollbar-color: var(--color-gray-500) white;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

body {
  scrollbar-gutter: stable;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

:focus-visible, .input:focus-within {
  outline-width: 0;
  outline-style: none;
}

* {
  scrollbar-width: thin;
  scrollbar-color: var(--color-base-300) transparent;
}

::-webkit-scrollbar {
  width: var(--spacing);
  height: var(--spacing);
}

::-webkit-scrollbar-track {
  background-color: #0000;
  border-radius: 50%;
}

::-webkit-scrollbar-corner {
  background-color: #0000;
}

::-webkit-scrollbar-thumb {
  background-color: var(--color-gray-500);
  border-radius: 1000px;
}

[x-cloak] {
  display: none !important;
}

.popover-menu {
  opacity: 0;
  transition-behavior: allow-discrete;
  margin: 0;
  transition: opacity .25s, display .25s, overlay .25s;
  display: none;
  position: absolute;
  inset: auto;
}

.popover-menu:popover-open {
  opacity: 1;
  display: grid;
}

@starting-style {
  .popover-menu:popover-open {
    opacity: 0;
    display: grid;
  }
}

.popover-right-top {
  top: anchor(top);
  left: anchor(right);
  position-try-fallbacks: --bottom;
}

.popover-bottom-right {
  top: anchor(bottom);
  right: anchor(right);
  position-try-fallbacks: --top;
}

@position-try --top {
  inset: auto; bottom: anchor(top) ; right: anchor(right) ;
}

@position-try --bottom {
  inset: auto; top: anchor(bottom) ; right: anchor(right) ;
}

@keyframes radio {
  0% {
    padding: 5px;
  }

  50% {
    padding: 3px;
  }
}

@keyframes skeleton {
  0% {
    background-position: 150%;
  }

  to {
    background-position: -50%;
  }
}

@keyframes progress {
  50% {
    background-position-x: -115%;
  }
}

@keyframes toast {
  0% {
    opacity: 0;
    scale: .9;
  }

  to {
    opacity: 1;
    scale: 1;
  }
}

@keyframes dropdown {
  0% {
    opacity: 0;
  }
}

@keyframes rating {
  0%, 40% {
    filter: brightness(1.05) contrast(1.05);
    scale: 1.1;
  }
}

